import React from 'react';

import Button from './Button';

const button = {
  'initialState': '',
  'resultState': '',
  'initialAlert': ' ',
  'resultAlert': ' '
}

class Book extends React.Component {
  render() {
    const { 
      book: { Title, ShortDescription, PageCount, Language, Progress, Cover } 
    } = this.props;

    return (
      <div style={styles.container}>
        <div style={styles.half}>
          <a href={Cover}>
            <img src={Cover} style={styles.picture}/>
          </a>
        </div>
        <div style={styles.half}>
          <h2 style={styles.title}>{Title}</h2>
          <div style={styles.pageCount}>- : {PageCount}</div>
          <div style={styles.ShortDescription}>{ShortDescription}</div>
          <div style={styles.Language}>: {Language}</div>
          <div style={styles.Progress}>: {Progress}%</div>
          <Button button={button} />
        </div>
      </div>
    )
  }
}

export default Book;

const styles = {
  container: {
    display: 'flex'
  },
  picture: {
    margin: '0 1em'
  }
}